}

/* Disabled */
.btn[disabled], input[disabled][type=submit],
.btn:disabled,
input[type=submit]:disabled {
  opacity: 0.55;
  cursor: not-allowed;
  box-shadow: none;
}

/* Icon affordance (SVG or <i>) */
.btn > svg, input[type=submit] > svg,
.btn > i,
input[type=submit] > i {
  inline-size: 1em;
  block-size: 1em;
}

/* Motion preference */
@media (prefers-reduced-motion: reduce) {
  .btn, input[type=submit] {
    transition: none;
  }
}
/* Submit buttons - arrow styling */
input[type=submit]:hover {
